import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Soft from '@/components/page/Soft'
import Study from '@/components/page/Study'
import Interlocution from '@/components/page/Interlocution'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/soft',
      name: 'Soft',
      component: Soft
    },
    {
      path: '/study',
      name: 'Study',
      component: Study
    },
    {
      path: '/question',
      name: 'Interlocution',
      component: Interlocution
    }
  ]
})



