<template>
  <div class="app-list" id="lists">
    <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" @bottom-status-change="handleBottomChange">
      <ul>
        <li v-for="item in list" class="list">
          <div class="list-item list-l">
            <img class="list-img" v-lazy.list=item.img>
          </div>
          <div class="list-item list-c">
            <div class="intro-title">最好的应用</div>
            <div class="intro-count">100W次下载<span class="blank-20"></span>12M</div>
            <div class="intro-content">超过100W用户超过100W用户超过100W用户超过100W用户</div>
          </div>
          <a :href="item.down"><div class="list-item list-r"><mt-button plain size="small">
            <p class="down-text">下载</p></mt-button></div></a>
        </li>
      </ul>
      <div class="" style="height: 50px;width: 100%;line-height: 50px;text-align: center">上滑加载更多...</div>
      <div slot="bottom" class="mint-loadmore-bottom">
        <span v-show="bottomStatus !== 'loading'" :class="{ 'rotate': bottomStatus === 'drop' }">↓</span>
        <span v-show="bottomStatus === 'loading'">Loading...</span>
      </div>
    </mt-loadmore>
  </div>
</template>

<script>
  let a = []
  let b = []

  import axios from 'axios'

  export default {
    name: 'soft',
    data () {
      return {
        msg: '',
        list: a,
        tPage: 1,
        allLoaded: false,
        bottomStatus: ''
      }
    },
    methods: {
      // jack = 'bzEt4rdZbxwL6Iy3EiByLChHWCziKlaOT93ME3Kl'
      handleBottomChange (status) {
        this.bottomStatus = status
      },
      loadBottom () {
        let _this = this
//        let jack = sessionStorage.getItem('jack')
        console.log(_this.tPage)
        axios({
          method: 'post',
          url: '/api/soft/list',
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          },
          data: {
            page: _this.tPage
          }
        }).then(function (response) {
          console.log(response)
          b = response.data.data
          _this.tPage += 1
          // console.log(b)
          if (_this.list.length > 0) {
            a = [...a, ...b]
          } else {
            a = b
          }
          _this.loading = true
          setTimeout(() => {
            _this.list = a
            _this.loading = false
          }, 20)
        }).catch(function (error) {
          console.log(error)
        })
        // this.allLoaded = true
        this.$refs.loadmore.onBottomLoaded()
      }

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus" scoped>

</style>



